<template>
  <div>
    <h2>Recipes Table</h2>
    <v-data-table
      :headers="headers"
      :items="s_list"
      :items-per-page="5"
      class="elevation-1"
    >
      <template v-slot:item.id="{ item }">
        <router-link :to="setLink(item.id)"
          ><v-chip dark>{{ item.id }}</v-chip></router-link
        >
      </template>
    </v-data-table>
    <router-link :to="newid"><v-btn>新規作成</v-btn></router-link>
  </div>
</template>

<script>
// @ is an alias to /src

import firebase from "firebase";
export default {
  name: "home",
  components: {},
  data() {
    return {
      mmark: "",
      db: "",
      s_list: [],
      headers: [
        { text: "id", value: "id" },
        { text: "content", value: "content" },
        { text: "date", value: "date" }
      ],
      newid: "/edit#0"
    };
  },
  created() {
    this.db = firebase.database();
    let that = this;
    this.db.ref("recipe/md").on("value", snapshot => {
      let s_keys = Object.keys(snapshot.val());
      let s_i = 0;
      let max = 0;
      for (let sval of snapshot.val()) {
        if (sval !== undefined) {
          that.s_list.push({
            id: s_keys[s_i],
            content: sval["content"],
            date: sval["date"]
          });
          if (max < Number(s_keys[s_i])) {
            max = Number(s_keys[s_i]);
          }
          s_i++;
        }
      }
      that.newid = "/edit#" + (max + 1);
    });
    //
    // let recipedb = this.db.ref("recipedb");
    // recipedb.on(
    //   "value",
    //   function(snapshot) {
    //     console.log(snapshot.val());
    //   },
    //   function(errorObject) {
    //     console.log("The read failed: " + errorObject.code);
    //   }
    // );
  },
  methods: {
    setLink(id) {
      return `/edit#${id}`;
    }
  }
};
</script>
